<template>
  <div class="login">
    <Titletop title="登录">
      <router-link slot="l" to="/index"></router-link>
      <router-link slot="r" to="/index"></router-link>
    </Titletop>
    <div class="logo"><img src="@/assets/鲸云小说/10002.png" /></div>
    <Forms>
      <van-form @submit="onSubmit">
        <van-field
          type="phone"
          v-model.trim="phone"
          placeholder="请输入手机号"
          name="phone"
        />
        <van-field
          v-model="password"
          type="password"
          name="password"
          placeholder="请输入密码"
        />
        <van-field name="checkbox" class="chekbox">
          <template #input>
            <van-checkbox v-model="checkbox" shape="square" />
            记住我
          </template>
        </van-field>
        <div style="margin: 16px 0px">
          <van-button round block type="info" native-type="submit" 
            >登录</van-button
          >
        </div>
        <div class="go"> <router-link to="/reg">注册账号</router-link> <router-link to="/forget">忘记密码</router-link></div>
      </van-form>
    </Forms>
  </div>
</template>

<script>
import Titletop from "@/components/Titletop.vue";
import Forms from "@/components/Forms.vue";
export default {
  components: { Titletop, Forms },
  name: "Mylogin",
  data() {
    return {
      checkbox: localStorage.getItem('remberMe'),
      phone: localStorage.getItem('loginname'),
      password: localStorage.getItem('loginpassword'),
    };
  },
  
  methods: {
    onSubmit(values) {
      if (!values.phone) {
        this.$toast("请输入手机号");
        return;
      }
      if (!values.password) {
        this.$toast("请输入密码");
        return;
      }
      this.axios
        .post("https://jyapi.jyacg.com/web/login", {
          phone: values.phone,
          password: values.password,
        })
        .then((res) => {
          if (res.status == 200) {
            if (res.data.code !== 0) {
              this.$toast(res.data.msg);
            }
            if(res.data.code == 0){
              localStorage.setItem('api_token',res.data.data)
              
              if(values.checkbox){
                localStorage.setItem('loginname',values.phone)
                localStorage.setItem('loginpassword',values.password)
              }else{
                localStorage.removeItem('loginname')
                localStorage.removeItem('loginpassword')
              }
              this.$router.replace({name:'index'})
            }
            
          }
        });
    },
  },
  watch:{
    checkbox(a){
      if(a){
        localStorage.setItem('remberMe',true)
      }else{
        localStorage.removeItem('remberMe')
      }
      
    }
  },
};
</script>

<style scoped lang="scss">
.go{
  display: flex;
  justify-content: space-between;

  a:nth-child(1){
    color: #1989fa;
  }
  a{
    color: #999;
  }
}
.login {
  height: 667px;
}
.logo {
  text-align: center;
  img {
    margin-top: 15px;
    width: 113px;
    height: 52px;
  }
}
</style>